/* Grande tablettes (portrait and landscape) ----------- */
@media only screen and (max-device-width : 1024px) {
    #root {
        header {
            .brand-logo {
                width: auto;
            }
            nav {
                height: auto;
                width: auto;
                visibility: hidden;
                position: absolute;
                z-index: 2;
                bottom: -153px;
                right: 61px;
                background-color: #100B20;

                ul {
                    flex-direction: column;
                    font-size: 0.9em;

                    li {
                        height: 40px;
                        width: 175px;
                        clip-path: none;     

                        .shine {
                            left: -55px;
                        }
                    }
                }
            }
            .languages-ctn {
                width: auto;

                .hamburger-menu {
                    display: block;
                }
            }
        }   
        main {
            .transition {
                display: none;
            }
    
            .about-ctr, .services-ctr, .cars-ctr, .booking-ctr {
                flex-direction: row-reverse;
                align-items: flex-end;
                scroll-margin-top: 50px;
    
                .about-main, .services-main, .cars-main {
                    height: 100%;
                    width: 100%;
                    padding-top: 0;
                    margin: 0;
    
                    .content-title {
                        padding: 0 0 0 125px;
                        margin-top: 40px;
                    }
    
                    .about-content{
                        height: calc(100% - 105px);

                        .banner {
                            width: 70%;
                            max-height: 250px;
    
                            &:nth-child(1) {
                                padding: 20px 0px 20px 100px;
                            }
    
    
                            p {
                                width: 90%;
                            }
                        }
                    }

                    .img-ctn {
                        .cars-slider {
                            img {
                                &:nth-child(2) {
                                    object-position: 30%;
                                }
                            }
                        }
                    }
    
                    .services-content, .cars-content {
                        height: calc(100% - 104px);
                        padding-left: 150px;
                    }

                    .cars-content {
                        height: calc(100% - 105px);
                        padding-left: 100px;

                        .left-info {
                            left: auto;
                            right: 0;

                            .cars-info {
                                right: 40px;
                            }
                        }
                    }
    
                    #book-link {
                        height: auto;
                        width: 100px;
                        min-width: auto;
                        flex-direction: column;
                        padding: 15px 7px;
    
                        svg {
                            margin: 0;
                        }
    
                        p {
                            font-size: 1.3em;
                        }
                    }   
                }

                .booking-main {
                    height: 100%;
                    width: 100%;
                    padding-top: 0;
                    margin: 0;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-end;

                    .static-info {
                        width: calc(100% - 100px);
                        margin-top: 40px;
                        text-align: center;
                    }

                    .form-container {
                        width: calc(100% - 100px);
                    }
                }  
    
                .about-nav, .services-nav, .cars-nav, .booking-nav {
                    height: 100%;
                    width: 100px;
                    position: absolute;
                    left: 0;
                    top: 0;
                    z-index: 1;
                    background-color: rgba(4, 6, 19, 0.63);
                    padding-top: 0;
                    display: flex;
                    flex-direction: column;
                    padding-top: 50px;
                    gap: 50px;
                    margin: 0;
    
    
                    .about-nav__title, .services-nav__title, .cars-nav__title, .booking-nav__title {
                        height: auto;
                        background: none;
    
                        .h-section {
                                writing-mode: vertical-lr;
                                text-orientation: upright;
                                font-family: 'Smooch Sans', sans-serif;
                                text-transform: uppercase;
                                font-size: 1.5em;
                        }
                    }
    
                    ul {
                        padding: 0;
    
                        li {
                            text-align: center;
                            padding: 0 15px;
    
                            &::before {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }
}

/* Petites tablettes (portrait and landscape) ----------- */
@media only screen and (max-device-width : 768px) {
    #root {
        main {
            .company-info-ctr {
                #company-slider {
                    .company-ctn {
                        .company-background {
                            filter: brightness(0.75);
                        }
                        .company-foreground{
                            justify-content: center;

                            .company-card {
                                width: 70%;
                                min-width: auto;
                                height: auto;
                                min-height: auto;
                                margin-left: 0;
                                box-shadow: none;
                                background: none;

                                .card-title {
                                    width: 100%;
                                    margin-bottom: 40px;
                                    white-space: pre-wrap;

                                    h2 {
                                        text-align: center;
                                    }
                                }

                                .card-info {
                                    margin-bottom: 45px;
                                }
                            }
                        }
                    }
                }

                .company-nav {
                    right: 0;
                }
            }
            .about-ctr {
                height: auto;
                min-height: 100vh;

                .about-main{
                    min-height: 100vh;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;

                    .img-ctn {
                        img {
                            filter: brightness(0.75);
                        }
                    }

                    .content-title {
                        margin-bottom: 40px;
                    }

                    .about-content{
                        .banner {
                            height: 250px;
                            width: 100%;
                            max-height: none;
                            margin-bottom: 40px;
                            box-shadow: none;
                        }

                        .right-banner {
                            padding: 20px 0px 20px 100px;
                            flex-direction: column;
                            // min-height: 248px;

                            ul {
                                flex-direction: row;
                                justify-content: space-evenly;
                                padding-left: 0;

                                li {
                                    font-size: 1.15em;
                                }
                            }
                        }
                    }
                }
            }

            .services-ctr {
                height: auto;
                min-height: 100vh;

                .services-main{
                    min-height: 100vh;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;

                    .content-title {
                        margin: 40px 0 20px 0;
                    }

                    .services-content {
                        height: calc(100% - 125px);
                        width: 100%;
                        padding: 0;
                        justify-content: flex-end;

                        .main-banner {
                            min-height: 500px;
                            width: calc(100% - 140px);
                            min-width: auto;
                            margin: 0 20px;
                            display: flex;
                            flex-direction: column;
                            justify-content: space-evenly;

                            .extras {
                                // flex-wrap: wrap;
                                li {
                                    height: 100px;
                                    width: 100px;
                                    margin: 0 5px;

                                    #extras-detailed {
                                        max-width: 300px;
                                    }
                                }
                            }
                        }

                        #book-link {
                            display: none;
                        }  
                    }
                }
            }

            .cars-ctr {
                height: auto;
                min-height: 100vh;

                .img-ctn {
                    .cars-slider {
                        align-items: flex-end;

                        img {
                            max-height: 90%;
                            object-position: 40%;

                            &:nth-child(1) {
                                max-height: 80%;
                                object-position: 20% 50px;
                            }

                            &:nth-child(2) {
                                max-height: 70%;
                                object-position: 20% 10px;
                            }
                        }
                    }
                }

                .cars-main {
                    min-height: 100vh;

                    .content-title {
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;
                        height: 175px;
                        margin-bottom: 20px;

                        .car-type {
                            margin-bottom: 20px;
                        }
                    }

                    .cars-content {
                        height: 543px;
                        justify-content: space-between;
                        padding: 0 3% 0 21%;

                        .left-info {
                            outline: 3px solid #040613;
                        }

                        .car-name-banner {
                            width: 50%;
                            height: 83%;
                            min-width: 160px;
                        }

                        .car-info-banner {
                            width: 40%;
                            height: 100%;
                            min-width: 160px;
                        }
                    }
                }
            }
        }

        footer {
            .footer-column {
                .footer-content {
                    li {
                        flex-direction: column;

                        svg {
                            margin: 0 0 7px 0;
                        }
                    }
                }
            }
        }

    }
}

/* Mobiles (portrait and landscape) ----------- */
@media only screen and (max-device-width : 480px) {
    #root {
        main {
            .company-info-ctr {
                #company-slider {
                    .company-ctn {
                        .company-foreground{
                            .company-card {
                                padding: 0;
                                .card-title {
                                    height: 96px;
                                    h2 {
                                        font-size: 2.5em;

                                        &::after {
                                            height: 50px;
                                        }
                                    }
                                }

                                .card-info p {
                                    font-size: 1.5em;
                                }
                            }
                        }
                    }
                }
            }

            .about-ctr {
                .about-main{
                    .content-title {
                        h3 {
                            font-size: 2.5em;
                            margin-right: 0;
                        }

                        svg {
                            display: none;
                        }
                    }

                    .about-content{
                        .banner {
                            height: auto;
                            min-height: 370px;

                            &:nth-child(1) {
                                padding: 20px 0px 20px 80px;
                            }
                        }

                        .right-banner {
                            padding: 20px 0px 20px 80px;

                            ul {
                                flex-wrap: wrap;
                                li {
                                    width: 33.33%;
                                }
                            }

                            .value-type {
                                width: 100%;
                                padding: 0 10%;
                            }
                        }
                    }
                }

                .about-nav {
                    width: 80px;

                    ul {
                        li {
                            font-size: 1.1em;
                            padding: 0;
                        }
                    }
                }
            }

            .services-ctr {
                .services-nav {
                    width: 80px;

                    ul {
                        li {
                            font-size: 1.1em;
                            padding: 0;
                        }
                    }
                }

                .services-main{
                    .content-title {
                        height: auto;
                        min-height: 65px;

                        h3 {
                            font-size: 2.5em;
                            margin-right: 0;
                        }

                        svg {
                            display: none;
                        }
                    }

                    .services-content {
                        .main-banner {
                            width: calc(100% - 80px);
                            margin: 0;
                            padding: 5%;

                            .extras {
                                flex-wrap: wrap;
                                li {
                                    font-size: 1.2em;
                                    width: 90px;

                                    #extras-detailed {
                                        max-width: 230px;
                                        bottom: 110px;

                                        .detailed-title {
                                            margin-bottom: 15px;
                                        }

                                        .detailed-description p{
                                            line-height: 25px;
                                        }
                                    }

                                    .li1 {
                                        left: 0;
                                    }
                                    .li2 {
                                        left: -116px;
                                    }
                                    .li3 {
                                        left: -105px;
                                    }
                                }
                            }
                        }

                        #book-link {
                            display: none;
                        }  
                    }
                }
            }

            .cars-ctr {
                .cars-main {
                    .img-ctn {
                        .cars-slider {
                            img {
                                &:nth-child(1) {
                                    max-height: 65%;
                                    object-position: 26% 0px;
                                }
    
                                &:nth-child(2) {
                                    max-height: 68%;
                                    object-position: 28% 0;
                                }
                            }
                        }
                    }
    
                    .content-title {
                        height: auto;
                        min-height: 175px;
                        padding: 0 0 0 100px;

                        h3 {
                            font-size: 2.5em;
                            margin-right: 0;
                        }

                        svg {
                            display: none;
                        }

                        .cars-question {
                            padding: 0 5%;

                            h4 {
                                font-size: 1.4em;
                            }
                        }
                    }

                    .cars-content {
                        flex-direction: column-reverse;
                        padding: 0 0 0 80px;

                        .left-info {
                            top: 370px;

                            .cars-info {
                                max-width: 250px;
                            }
                        }

                        .car-name-banner {
                            width: 70%;
                            height: 80%;
                            align-self: center;
                        }

                        .car-info-banner {
                            width: 85%;
                            height: 80px;
                            display: flex;
                            align-items: flex-end;
                            overflow: hidden;
                            box-shadow: inset 15px 0px 0px 0px #C3C3C3;

                            ul {
                                display: flex;
                                flex-direction: column;
                                height: 100%;
                                padding: 0 0 0 30px;
                                animation: slideInfoCars 10s linear infinite;

                                li {
                                    display: flex;
                                    align-items: center;
                                    height: 80px;
                                    width: 100%;
                                    flex-shrink: 0;
                                    margin: 0;

                                    &:last-child{
                                        display: flex;
                                    }
                                }
                            }
                        }
                    }
                }

                .cars-nav {
                    width: 80px;

                    ul {
                        li {
                            font-size: 1.1em;
                            padding: 0;
                        }
                    }
                }
            }

            .booking-ctr {
                .booking-nav {
                    width: 80px;

                    ul {
                        li {
                            font-size: 1.1em;
                            padding: 0;
                        }
                    }
                }

                .booking-main {
                    .static-info, .form-container {
                        width: calc(100% - 80px);
                        padding: 0 5%;
                    }

                    .static-info p {
                        margin: 0;
                    }

                    .form-container {
                        margin: 40px 0;
                        padding: 0 5% 0 0;

                        form {
                            outline: none;
                            padding: 30px 5% 30px 10%;
                            margin: 0;
                            width: 100%;

                            .input-container {
                                align-items: flex-start;

                                #date {
                                    width: 80%;
                                }
                            }

                            .basic-info {
                                .error, label {
                                    width: 100%;
                                }

                                input {
                                    width: 75%;
                                }
                            }

                            .message-container {
                                label, textarea {
                                    width: 100%;
                                }
                            }
                        }
                    }
                }
            }
        }

        footer {
            display: flex;
            flex-direction: column;

            .footer-column {
                width: 100%;
            }

            .legal-notice {
                flex-direction: column;

                p {
                    margin-bottom: 25px;
                    width: 100%;

                    &:nth-child(1) {
                        margin: 25px 0 25px 0;
                    }
                }
            }
        }
    }
}

@keyframes slideInfoCars {
    0%, 15% {
        transform: translateY(0);
    }
    20%, 35% {
        transform: translateY(-80px);
    }
    40%, 55% {
        transform: translateY(-160px);
    }
    60%, 75% {
        transform: translateY(-240px);
    }
    80%, 95% {
        transform: translateY(-320px);
    }
    100% {
        transform: translateY(-400px);
    }
}
